.cards__wrapper {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

.cards__photo {
  position: relative;
}

.cards__photo--big {
  width: 482px;
  margin-top: -67px;
  margin-right: 15px;
}

.cards__photo--middle {
  width: 411px;
}

.cards__photo--small {
  width: 211px;
  margin-right: 30px;
}

.cards__photo--skills span {
  max-width: 320px;
}

.cards__photo--creative,
.cards__photo--leadership {
  position: absolute;
  bottom: 0;
}

.cards__photo--creative {
  left: 496px;
  bottom: -1px;
}

.cards__photo--leadership {
  right: 2px;
  margin-right: 0;
}

.cards__desc {
  position: absolute;
  left: 32px;
  bottom: 37px;
  width: 360px;
  color: $color-white;
  font-family: $font-raleway;
  font-size: 21px;
  font-weight: 800;
  line-height: 24.65px;
}

.cards__desc--big {
  left: 40px;
}

.cards__desc--middle {
  left: 33px;
  bottom: 36px;
  width: 360px;
}

.cards__desc--small {
  left: 24px;
  bottom: 36px;
  width: 120px;
}

@media (max-width: $desktop-width-1365) {
  .cards {
    justify-content: center;
  }

  .cards__photo--big {
    width: 395px;
  }

  .cards__photo--middle {
    width: 330px;
  }

  .cards__photo--small {
    width: 170px;
  }

  .cards__photo--creative {
    left: 420px;
  }

  .cards__photo--leadership {
    right: 10px;
  }

  .cards__desc {
    max-width: 300px;
    font-size: 16px;
  }
}

@media (max-width: $tablet-width) {
  .advantages__wrapper {
    max-width: 640px;
  }

  .cards__photo--big {
    width: 260px;
    margin-top: -40px;
  }

  .cards__photo--middle {
    width: 220px;
  }

  .cards__photo--small {
    width: 115px;
  }

  .cards__photo--skills span {
    max-width: 188px;
  }

  .cards__photo--creative {
    left: 276px;
  }

  .cards__photo--leadership {
    right: 0;
  }

  .cards__desc {
    left: 20px;
    bottom: 20px;
    max-width: 188px;
    font-size: 11px;
  }

  .cards__desc--small {
    max-width: 80px;
  }
}

@media (max-width: $mobile-width) {
  .cards {
    overflow: hidden;
    margin-bottom: 40px;
  }

  .cards__wrapper {
    flex-wrap: nowrap;
    min-height: 417px;
  }

  .cards__photo {
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    width: 100%;
    margin-right: 0;
    text-align: center;
  }

  .cards__photo--big {
    margin-top: 0;
  }

  .cards__photo--creative,
  .cards__photo--leadership {
    position: static;
  }

  .cards__photo img {
    width: 100%;
    height: auto;
  }

  .cards__desc {
    position: static;
    display: block;
    max-width: 225px;
    margin: 0 auto;
    margin-top: -91px;
    margin-bottom: 20px;
    text-align: center;
    font-size: 18px;
    line-height: 21px;
  }
}
